import * as util from 'util';
import * as color from 'ansi-colors';

import { stdout, clear } from './stdout';


const __CustomizeColorConf = {
  info     : color.cyan,
  warn     : color.yellow,
  error    : color.red,
  success  : color.green,
  dark     : color.dim.gray,

  disabled : color.gray,
  em       : color.italic,
  heading  : color.bold.underline,
  muted    : color.dim,
  primary  : color.blue,
  strong   : color.bold,
  underline: color.underline,
};


function Kitty(options = {}) {

  if (!(this instanceof Kitty)) {
    throw new TypeError("Cannot call a class as a function");
  }

  const _privater_ = {};

  let printerMapping = { ...options };

  class Cat {

    constructor() {}

    clear = clear
  }

  Object.keys(printerMapping).forEach((method)=>{

    if (method in Cat.prototype) return;

    (Cat.prototype)[method] = function proxyAgent(...output) {
      stdout({ output, method, options });
    }
  });

  return new Cat(options);
};


/* -------------------------------------------------------------------------- */
export default new Kitty({
  log    : '[:datetime][:method] %s [asdfas]',
  info   : '#blue(:i-info)[:datetime][:method]',
  success: '#green(:i-check)[:datetime]',
  warn   : '#yellow(:i-excl)[:time]警告 $yellow',
  error  : '#redBright(:i-cross)[:datetime][:method]'
});


export const Cat = Kitty;